/* ==========================================================================
   Instagram widget style - begin
   ========================================================================== */

aside.eltdf-sidebar,
.wpb_widgetised_column {
	
	.widget {
		
		&.widget_eltdf_instagram_widget {
			
			.eltdf-widget-title {
				margin: 0 0 25px;
			}
		}
	}
}

.widget_eltdf_instagram_widget{
	ul{
		margin: 0 -5px 35px!important;
	}
}

.eltdf-instagram-feed {
	list-style: none;
	padding: 0;
	margin: 0;
	
	li {
		float: left;
		box-sizing: border-box;
		border: none !important;

		a {
			position: relative;
			display: block;
			overflow: hidden;
			
			
			.eltdf-instagram-icon {
				position: absolute;
				display: flex;
				align-items: center;
				align-content: center;
				justify-content: center;
				justify-items: center;
				top: 50%;
				left: 50%;
				background-color: #fff;
				width: 86%;
				height: 86%;
				color: $default-heading-color;
				z-index: 1;
				transition: opacity .2s ease-in-out;
				transform: translate(-50%, -50%);
				-webkit-clip-path: inset(0 0 100% 0);
				clip-path: inset(0 0 100% 0);
				transition: .2s cubic-bezier(0.48, 0.57, 0.33, 0.89);

				>span{
					transform: rotateZ(45deg);
					color: $first-main-color;
				}
			}
			
			&:hover {
				
				.eltdf-instagram-icon {
					-webkit-clip-path: inset(0 0 0 0);
					clip-path: inset(0 0 0 0);
					transition: .4s cubic-bezier(0.48, 0.57, 0.33, 0.89);
				}
			}
		}
		
		img {
			width: 100%;
			display: block;
		}
	}
	
	&.eltdf-instagram-gallery {
		$gallery_space_label: ('no', 'tiny', 'small', 'normal');
		$gallery_space_width: (0, 5, 10, 15);
		
		@for $i from 0 to length($gallery_space_label) {
			&.eltdf-#{nth($gallery_space_label,$i+1)}-space {
				$column_width: nth($gallery_space_width, $i+1);
				$column_width_margin: $column_width * 2;
				
				@if ($column_width == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$column_width}px -#{$column_width_margin}px;
				}
				
				li {
					padding: 0 #{$column_width}px;
					margin: 0 0 #{$column_width_margin}px;
				}
			}
		}
		
		$gallery_columns: ('2', '3', '4', '6', '9');
		@for $i from 0 to length($gallery_columns) {
			&.eltdf-col-#{nth($gallery_columns, $i+1)} {
				$column_width: 100%/($i+2);
				
				@if ($i == 3) {
					$column_width: 100%/6;
				} @else if ($i == 4) {
					$column_width: 100%/9;
				}
				
				li {
					width: $column_width;
				}
			}
		}
	}
	
	&.eltdf-instagram-carousel {
		
		li {
			position: relative;
			width: 100%;
			margin: 0;
			padding: 0;
			
			a {
				position: relative;
				display: block;
				height: 100%;
			}
		}
	}
}

/* ==========================================================================
   Instagram widget style - end
   ========================================================================== */